<template lang="pug">
    #content-selector
        .option(v-on:click="selected='Projects'") Projects <br>
        .option(v-on:click="selected='SlowMovementZones'")| Slow movement zones
</template>

<script>
    export default {
        data() {
            return {
                selected: undefined
            }
        },
        watch: {
            selected(val) {
                window.app.ProjectList.shown = val === 'Projects';
                window.app.SlowMovementZones.shown = val === 'SlowMovementZones';
                window.app.Map.switchContent(val);
            }
        }, mounted() {
            window.app.ContentSelector = this;
            this.selected = 'Projects';
            // this.selected = 'SlowMovementZones';
        }
    }
</script>

<style lang="less" scoped>
