<template lang="pug">
#project-list(v-show="shown")
    //- .container
    h2 Completed Projects
        //- img.marker(src="markers/darkblue-map-marker.png")
    .project(v-for="project in projectsBy('Complete')" v-on:click="onClick(project)")
        .name {{ project.properties.name }}
        .type {{ project.properties.type }}
    h2 Underway
    .project(v-for="project in projectsBy('Underway')" v-on:click="onClick(project)")
        | {{ project.properties.name }}
    h2 Planned
    .project(v-for="project in projectsBy('Planned')" v-on:click="onClick(project)")
        | {{ project.properties.name }}
</div>
</template>
<script>
export default {
    name: 'ProjectList',
    data: function() {
        return {
            shown: true
        }
    }, computed: {
        projects() { 
            // console.log(window.app.kunshan);
            return window.data.projectPoints.features
                // .filter(feature => feature.properties.images); 
            },
    }, mounted() {
        window.app.ProjectList = this;
    }, methods: {
        projectsBy(status) { return this.projects.filter(p => p.properties.status === status); },
        onClick(project) {
            console.log(project.properties);
            const fullProject = window.data.projects.features.find(p => p.id === project.id);
            window.app.Map.highlightFeature(fullProject);
            // window.app.ProjectList.
            window.app.ProjectDetail.project = project.properties;
        }
    }
};
</script>

