<template lang="pug">
#project-detail
    //- (v-show="name")
    div(v-if="name")
        h2.name {{ name.toLowerCase() }}
        .status(v-if="project.status") Status: {{ project.status }}
        .type(v-if="project.type") Type: {{ project.type }}
        p.description(v-html="project.description")
        carousel(:navigationEnabled="true", :perPage=1, v-show="images.length")
            slide(v-if="project.video")
                iframe(:src="video" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
            slide(v-for="(image,i) in images", :key="image.url")
                .image-wrapper
                    img.img-small(:src="'images/' + image.url")
                    p.caption(v-html="image.description_en")


</div>
</template>

<script>
import { Carousel, Slide } from 'vue-carousel';
export default {
    components: {
        Carousel,
        Slide
    },
    name: 'ProjectDetail',
    data: function() {
        return {
            project: {},
            fullProject: {},
            language: 'en'
        }
    },
    computed: {
        // actually no, we should generate two datasets, one English, one Chinese
        name () { return this.language === 'en' ? this.project.name : this.project.name_cn },
        images() {
            if (typeof this.project.images === 'string') {
                // I don't understand why sometimes this property becomes a string
                return JSON.parse(this.project.images || '[]')
            } else {
                return this.project.images || [];
            }
        }, video() {
            return this.project.video.replace(/^https?:\/\//, '//').replace(/\/watch\?v=/, '/embed/');
        }
    },
    mounted() {
        window.app.ProjectDetail = this;
    },
    props: {
      
    }
};
</script>

