<template lang="pug">
#map-div
</div>
</template>
<script src="lib/mapbox-gl-dev.js"></script>
<script>
// const mapboxgl = require('mapbox-gl');
const turf = require('@turf/turf');
const thenify = require('thenify');
const color = require('color');
const offline = window.location.hash.match('offline');

const devMode = window.location.host.match(/localhost/) && !window.location.hash.match(/prod/);
export default {
    name: 'Map',
    data() {
        return {
            map: undefined,
            highlightedFeature: undefined,
            popup: undefined,
            slowMovementZone: undefined
        };
    },
    props: {

    }, methods: {
        panTo(feature, zoom=14) {
            const center = turf.centroid(feature).geometry.coordinates;
            this.map.flyTo({ center, zoom });
        },
        highlightFeature(feature, pan = true) {
            if (!feature) {
                this.map.U.update('highlight', turf.featureCollection([]));
                return;
            }
            if (pan) {
                this.panTo(feature);
            }
            map.U.update('highlight', feature);
            
        }, switchContent(content) {
            if (this.map) {
                this.map.U.toggle(['project-points'], content === 'Projects');
                this.map.U.toggle(['slow-movement-core','slow-movement-halo'], content === 'SlowMovementZones');
            }
        }
    }, mounted() {
        window.app.Map = this;
        window.setTimeout(initMap.bind(this),  1000);
    }, watch: {
        slowMovementZone(zone) {
            this.map.U.onLoad(() => {
               this.map.U.update('slow-movement-zones', zone)
            });
        }
    }
};


function initMap() { 
    const colors = {
        darkblue: '#3e5798',
        teal: '#006670',
        blue: '#35afe4',  
        red: '#ee4945',
        orange: '#f27121',
        mandarine: '#faad49',
        lightgreen: '#5ee2a0',
        purple: '#a3a0fb'
    };

    function loadMarkerImages() {
        const loadImage = thenify(map.loadImage).bind(map);
        const markerNames = ['lightgreen-map-marker', 'red-map-marker', 'teal-map-marker', 'orange-map-marker', 'blue-map-marker', 'darkblue-map-marker', 'mandarine-map-marker'];
        return Promise.all(markerNames.map(name => 
            loadImage('markers/' + name + '.png')
            .then(image => map.addImage(name, image))
        ));
    }

    function addProjectPoints() {
        map.U.addSymbol('project-points', window.data.projectPoints, {
            iconImage: ['match', ['get', 'status'],
                'Planned', 'lightgreen-map-marker',
                'Underway', 'mandarine-map-marker',
                'Complete', 'darkblue-map-marker',
                'red-map-marker'
            ],
            iconSize: { stops: [
                [11, 0.25],
                [13, 0.5]
            ]},
            iconAnchor: 'bottom',
            ...(offline ? {} : {
                textField: '{name}',
                textSize: 14,//['interpolate', ['linear'], ['zoom'],
                textOffset: [1, 0],
                textColor: '#333',//colors.blue, // primary-blue
                textHaloColor: 'hsla(0,0%,95%,0.7)',
                textHaloWidth: 1,
                textAnchor: 'bottom-left',
                textJustify: 'left',
                textOptional: true,
                textOpacity: { stops: [
                    [12, 0],
                    [13, 1]
                ]}
            }),
            // 'text-font': ['Open Sans Bold']
            minzoom: 11
        });

    }

    function addSlowMovementLayers() {
        map.U.addGeoJSON('slow-movement-zones');
        map.U.addLine('slow-movement-halo', 'slow-movement-zones', {
            lineWidth: ['interpolate', 
                ['exponential', 1.5], ['zoom'], 
                10, 5,
                15, 20
            ],
            lineColor: colors.purple,
            lineOpacity: 0.5,
            lineJoin: 'round',
            lineCap: 'round',
            filter: ['==', '$type', 'LineString']
        });
        map.U.addLine('slow-movement-core', 'slow-movement-zones', {
            lineWidth: ['interpolate', 
                ['exponential', 1.5], ['zoom'], 
                10, 2,
                15, 5
            ],
            lineColor: color(colors.purple).darken(0.3).string(),
            lineOpacity: 1,
            lineJoin: 'round',
            lineCap: 'round',
            filter: ['==', '$type', 'LineString']
        });
    }

    function addBuildings() {
        map.U.addFillExtrusion('buildings', 'mapbox://ausgreens.cjprdamej17bc2vny3mvwkfep-2d6g3', { //'window.data.buildings, {
            sourceLayer: 'Kunshan-buildings',
            fillExtrusionHeight: 30,
            fillExtrusionOpacity: 0.5,
            fillExtrusionColor: 'hsl(60, 100%, 70%)'
        });
    }

    function addHighlightLayers() {
        map.U.addGeoJSON('highlight');
        const highlightColor = colors.blue;

        map.U.addCircle('highlight-circle', 'highlight', {
            circleColor: 'transparent',
            // circleOpacity: ['case', ['to-boolean', ['feature-state', 'highlighted']], 0.5, 0],
            circleRadius: 20,
            circleStrokeColor: highlightColor,
            circleStrokeWidth: 10,
            circleOpacity:0.8,
            circleBlur: 0.2,
            filter: ['==', '$type', 'Point']
        });        
        map.U.addLine('highlight-line', 'highlight', {
            lineColor: highlightColor,
            // lineOpacity: ['case', ['to-boolean', ['feature-state', 'highlighted']], 0.5, 0],
            lineWidth: 20,
            lineOpacity: 0.8,
            lineJoin: 'round',
            lineCap: 'round',
            lineBlur: 3,
            filter: ['in', '$type', 'LineString', 'Polygon']
        });
        map.U.addLine('project-line', 'highlight',  {
            lineColor: color(colors.lightgreen).darken(0.3).string(),
            lineWidth: 5,
            filter: ['==', '$type', 'LineString']
        });
    }
                
    function addProjectLayers() {
        map.U.addFill('project-fill', 'projects', { 
            fillColor: color(colors.lightgreen).lighten(0.2).string(),
            fillOpacity: 0.3,
            filter: ['==', '$type', 'Polygon'],
            // outlineColor: color(colors.lightgreen).darken(0.2).string()
        });
        map.U.addLine('project-fill-border', 'projects', { 
            lineColor:color(colors.lightgreen).darken(0.4).string(),
            filter: ['==', '$type', 'Polygon'],
        });

        0 && map.U.addCircle('project-circle', 'projects', {
            circleColor: ['match', ['get','type'],
                'project', 'red',
                'park', 'lightgreen',
                'wetland','green',
                '#ddd'] ,
            circleStrokeColor: 'black',
            circleStrokeWidth: 1,
            filter: ['==', '$type', 'Point']
        });
    }

    mapboxgl.accessToken = 'pk.eyJ1Ijoic3RldmFnZSIsImEiOiJjam5kcGMzNjMydnNxM2ttc2toamo3Yzk2In0.JLncOwxdMGtouRcPNJtnbQ';
    let style;
    if (offline) {
        style = {
                version: 8,
                sources: {},
                layers: []
            }
    } else if (devMode) {
        style = 'mapbox://styles/stevage/cjm4redogbbrk2rp4r3cyhy1q';
    } else {
        style = 'mapbox://styles/stevage/cjntir8r53vuo2spihcvac3t8';
    }
    let map = new mapboxgl.Map({
        container: 'map-div', 
        // style: 'mapbox://styles/mapbox/streets-v9', 
        // style: 'mapbox://styles/stevage/cjndpdker1i8c2spa5esdmphe', // reduced
        // style: 'mapbox://styles/stevage/cjntir8r53vuo2spihcvac3t8', // light
        style: style,
        center: [120.902678, 31.38549], 
        zoom: devMode ? 14 : 11.5,
        // pitchWithRotate: true,
        // dragRotate: false,
        touchZoomRotate: false
    }).addControl(new mapboxgl.NavigationControl({ showCompass: false }), 'top-right');
    const U = require('mapbox-gl-utils').init(map);
    window.map = map;
    
    map.U.onLoad( () => {
        addBuildings();
        addHighlightLayers();
        map.U.addGeoJSON('projects', window.app.kunshan);
        addProjectLayers();
        loadMarkerImages().then(addProjectPoints);
        addSlowMovementLayers();
        map.on('click', e => {
            const project = map.queryRenderedFeatures(e.point, { layers: ['project-points', 'project-fill'] })[0];
            if (project) {
                window.app.ProjectDetail.project = project.properties;
                const fullProject = window.data.projects.features.filter(p => p.id === project.id)[0];
                this.highlightFeature(fullProject);
            } else {
                window.app.ProjectDetail.project = {};
                this.highlightFeature();
            }
        });
        map.U.hoverPointer(['project-points', 'slow-movement-halo', 'project-fill']);
    });
    this.map = map;
    this.popup = new mapboxgl.Popup();
}
//<style src="../../node_modules/mapbox-gl/dist/mapbox-gl.css"></style>

require('../../node_modules/mapbox-gl/dist/mapbox-gl.css');

</script>

<style>

#map-div {
    height: 100%;
    background:grey;
}

</style>
