<template lang="pug">
#slow-movement-zones(v-show="shown")
    h2 Slow Movement Zones
    .zone.project(v-for="zone in zones" v-on:click="show(zone)")
        | {{ zone.properties.name.toLowerCase() }}
</template>

<script>
    export default {
        data() {
            return {
                shown: false,
                zones: require('../data/slow-movement.json').features
            }
        },
        methods: {
            show(zone) {
                window.app.Map.slowMovementZone = zone;
                window.app.ProjectDetail.project = zone.properties;
                window.app.Map.panTo(zone, 13);
            }
        },
        mounted() {
            window.app.SlowMovementZones = this;
        }
        
    }
</script>

<style lang="less" scoped>
@import "vars.less";
#slow-movement-zones {
    background:@primary-teal;//hsl(185, 100%, 34%);
    color: white;
}
// Other styles in ProjectList, .project
.zone {
    text-transform: capitalize;
}
</style>